"use client";
import React, { useState, useEffect } from "react";

// Products Section
const ProductsSection = () => {
  const [activeCategory, setActiveCategory] = useState("business");

  const categories = [
    { id: "business", name: "Business" },
    { id: "marketing", name: "Marketing" },
    { id: "promotional", name: "Promotional" },
  ];

  const products = {
    business: [
      "Business Cards",
      "Letterheads",
      "Envelopes",
      "ID Cards",
      "Corporate Folders",
      "Notebooks",
      "Diaries",
      "Carbonless Forms",
    ],
    marketing: [
      "Brochures",
      "Flyers",
      "Catalogs",
      "Posters",
      "Banners",
      "Calendars",
      "Annual Reports",
      "Magazines",
    ],
    promotional: [
      "T-Shirts",
      "Bags",
      "Mugs",
      "Key Chains",
      "Wall Clocks",
      "Labels",
      "Flags",
      "Stickers",
    ],
  };

  return (
    <section id="products" className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            Comprehensive Product Catalog
          </h2>
          <p className="text-xl text-gray-600 max-w-2xl mx-auto">
            From business essentials to promotional items—we print it all
          </p>
        </div>

        <div className="flex flex-wrap justify-center gap-4 mb-12">
          {categories.map((category) => (
            <button
              key={category.id}
              onClick={() => setActiveCategory(category.id)}
              className={`px-8 py-3 rounded-full font-medium transition-all ${
                activeCategory === category.id
                  ? "bg-linear-to-r from-blue-600 to-purple-600 text-white shadow-lg"
                  : "bg-gray-200 text-gray-700 hover:bg-gray-300"
              }`}
            >
              {category.name}
            </button>
          ))}
        </div>

        <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
          {products[activeCategory].map((product, index) => (
            <div
              key={index}
              className="bg-linear-to-br from-gray-50 to-gray-100 rounded-xl p-6 text-center hover:shadow-lg transition-shadow cursor-pointer group"
            >
              <div className="w-16 h-16 bg-linear-to-br from-blue-500 to-purple-600 rounded-full mx-auto mb-4 flex items-center justify-center group-hover:scale-110 transition-transform">
                <span className="text-white text-2xl font-bold">
                  {product.charAt(0)}
                </span>
              </div>
              <h3 className="font-semibold text-gray-900">{product}</h3>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ProductsSection;